<template>
    <table cellspacing="0" cellpadding="0">
        <tbody v-if="type=='in'">
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="(item,index) in list" :class="{tr2:index%2}">
                <td>{{item.date}}</td>
                <td>{{item.recharge_cost}}</td>
                <td>{{item.recharge_before_balance}}</td>
                <td>{{item.recharge_after_balance}}</td>
            </tr>
        </tbody>

        <tbody v-if="type=='out'">
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="(item,index) in list" :class="{tr2:index%2}">
                <td>{{item.created_at}}</td>
                <td>{{item.type | fiterCode}}</td>
                <td>{{item.consume_money}}</td>
                <td>{{item.before_consume_money}}</td>
                <td>{{item.after_consume_money}}</td>
            </tr>
        </tbody>
    </table>
</template>
<script>
    import API from 'src/services/api'

    export default {
        props: ['list', 'type'],
        data() {
            let user = JSON.parse(localStorage.getItem('user'))
            return {
                userType: user.type,
            }
        },
        filters: {
            fiterCode(value) {
                switch (value) {
                    case 1:
                        return ''
                    case 2:
                        return ''
                    case 3:
                        return ''
                    case 4:
                        return ''
                    default:
                        return ''
                }
            }
        }
    }

</script>


